<template>
  <div class="invoice padd">
      <h1 class="large_t">发票</h1>
      <div class="belone clearfix">
          <span class="two_t_m fl">发票抬头</span>
          <span @click="isPerson=true" class="fl per" :class="[isPerson ? 'default' : 'conpany']">个人</span>
          <span @click="isPerson=false" class="fl" :class="[!isPerson ? 'default' : 'conpany']">单位</span>
      </div>
      <div class="detail">
          <ul>
              <li v-show="isPerson">
                <input type="text" placeholder="个人">
              </li>
              <li v-show="!isPerson">
                <input type="text" placeholder="公司名称">
              </li>
              <li v-show="!isPerson">
                <input type="text" placeholder="纳税人识别号">
              </li>
              <li class="clearfix">
                  <span class="two_t_m fl">接收号码</span>
                  <input type="number" class="fl teshu" placeholder="手机号码">
              </li>
              <li class="clearfix">
                  <span class="two_t_m fl">接收邮箱</span>
                  <input type="text" class="fl teshu" placeholder="电子邮箱">
              </li>
          </ul>
      </div>
      <div class="bottom">
          <p class="three_t_m">* 发票须知：<br>1、得乐开具的均为电子发票，电子普通发票时税局认可的有效凭证，其法律效力、基本用途均与纸质发票相同。<br>2、开票金额为您实际支付金额。（不包含礼券、打折优惠价等优惠金额）</p>
          <button class="bluebtn" @click="$router.push('/shoppingBag/comfirm')">开具发票</button>
      </div>
  </div>
</template>

<script>
export default {
  name: "Invoice",
  data() {
    return {
      isPerson: true
    };
  },
  methods: {},
  components: {}
};
</script>

<style scoped lang="less">
.invoice {
  font-size: 0.16rem;
  .belone {
      padding-top: 0.15rem;
    .per {
      margin: 0 0.2rem 0 0.14rem;
    }
    .default {
      font-weight: 500;
      font-family: PingFangSC-Regular;
      font-size: 0.12rem;
      color: #191919;
      letter-spacing: 0;
      padding: 0.015rem 0.08rem;
      border: 0.01rem solid rgba(0, 0, 0, 0.9);
      border-radius: 0.03rem;
    }
    .conpany {
      font-weight: 500;
      font-family: PingFangSC-Regular;
      font-size: 0.12rem;
      color: #cccccc;
      letter-spacing: 0;
      padding: 0.015rem 0.08rem;
      border: 0.01rem solid rgba(0, 0, 0, 0.9);
      border-radius: 0.03rem;
      border: 0.01rem solid rgba(0, 0, 0, 0.2);
      border-radius: 0.03rem;
    }
  }
  .detail {
    ul {
      li {
        border-bottom: 0.01rem solid #f2f2f2;
        input {
          width: 100%;
          border: none;
          height: 0.22rem;
          margin: 0.15rem 0 0.16rem 0;
          font-size: 0.16rem;
          line-height: 0.22rem;
        }
        .teshu {
          width: 2.42rem;
          margin-left: 0.14rem;
        }
        span {
          margin: 0.14rem 0rem;
        }
      }
    }
  }
  .bottom {
      p {
          margin: 0.15rem 0 0.3rem 0;
      }   
  }
}
</style>



